<template>
    <div class="my-popups">
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">
                            Request Product: {{ ItemData.Name }}
                        </h5>
                        <button v-on:click="onCancel" type="button" class="close" data-original-title="Close"><em
                                class="fa fa-times"></em></button>

                    </div>
                    <div class="border p-3">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                            <template v-slot:tgslot-NumberofProducts="{ data }">


                                <Field name="Number of Products" :rules="{ 'required': true }" v-slot="{ errors }">
                                    <div class="input-group">
                                        <input type="number"
                                            v-on:keyup="checkQuantity($event, data.fieldInfo, data.fieldInfo.value)"
                                            id="" v-model="data.fieldInfo.value" min="1" maxlength="8"
                                            v-bind:class="{ 'txtitems form-control ': true, 'is-invalid': errors[0] || data.fieldInfo.NumberOfItems == 0 ? true : false }" />
                                        <span class="input-group-append">
                                            <span class="input-group-text" id="spnOutOF">/Out of {{
                                ItemData.Available_Quantity }}</span>
                                        </span>
                                    </div>
                                    <span v-if="errors[0]" name="Number of Products" class="invalid-feedback d-block">{{
                                errors[0]
                                        }}</span>

                                </Field>


                            </template>
                        </dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import DataService from '../../service/DataService';

export default {
    props: {
        ItemData: {
            type: Array,
            required: true
        }
    },
    data() {
        return {
            IsShowAddNamePopup: false, Title: '',
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('Product'),
                            name: "Product",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],

                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Unique Name'),
                            name: "10/12/2022 12:AM",
                            value: "",
                            placeholder: "",

                        },
                        {
                            astype: "DateField",
                            label: this.$t('AssignedFrom'),
                            name: "AssignedFrom",
                            value: '',
                            placeholder: "",
                            config: { format: "DD/MM/YYYY" }
                        },


                        {
                            astype: "DateField",
                            label: this.$t('AssignedTill'),
                            name: "AssignedTill",
                            value: '',
                            placeholder: "DD/MM/YYYY",
                            config: { format: "DD/MM/YYYY" }
                        },

                        {
                            astype: "SlotField",
                            label: this.$t('Number of Products'),
                            name: "NumberofProducts",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 250 },

                        }

                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('Remarks'),
                            name: "Remarks",
                            value: "",
                            placeholder: "",
                            config: {
                                rows: 5
                            }
                        },
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },

                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.onCancel
                }
            ],
        }
    },
    created: function () {
        this.FetchData();
        this.GetAvailableQty();
    },
    methods: {
        FetchData: function () {
            console.log(this.ItemData);
            (this.FormSchema)[0].Data[0].value = this.ItemData.Name;
            (this.FormSchema)[0].Data[1].value = this.ItemData.UniqueName;
            (this.FormSchema)[0].Data[2].value = this.ItemData.AssignedFrom;
            (this.FormSchema)[0].Data[3].value = this.ItemData.AssignedTill;
        },
        GetAvailableQty: function () {
            var url = `relatedInfoID=${this.ItemData.RelatedInfoId}`
            DataService.GetAvailableQty(url).then(response => {
                (this.FormSchema)[0].Data[4].value = response.data;
            });
        },
        CloseRequestForQuantity: function () {
            this.$emit('CloseRequestForQuantity')
        },
        onCancel: function () {
            this.$parent.isRequestForQuantity = false;
        },
        checkQuantity(event, data, value) {

            var vm = this;
            let currentQuantity = value;

            if (parseInt(currentQuantity) > vm.ItemData.Available_Quantity) {
                ////
                vm.FormSchema[0].Data[4].value = null;
                vm.ShowAlert(vm.$t('Please enter a value less than or equal to Available quantity'), "failure", true, vm.$t('Alert'));

            }
            else {
                //
                vm.FormSchema[0].Data[4].value = currentQuantity;
                return
            }
        },
        onSubmit(formfields, val) {
            var vm = this;
            vm.isLoading = true;
            let JsonObj = {
                Name: vm.ItemData.Name,
                UniqueName: vm.ItemData.UniqueName,
                AssignedFrom: formfields.AssignedFrom,
                AssignedTill: formfields.AssignedTill,
                NumberOfItems: formfields.NumberofProducts,
                Remarks: formfields.Remarks,
                RelatedInfoId: vm.ItemData.RelatedInfoId,
                AssetAssignmentId: vm.ItemData.AssetAssignmentId,
                UserId: vm.ItemData.UserId,
                AssetCatalogueId: vm.ItemData.AssetCatalogueId,
                AssetReplacementType: vm.ItemData.AssetReplacementType,
            };
            var Jsondata = JSON.stringify(JsonObj);
            DataService.AssetRequest(Jsondata).then(response => {
                if (response.data != null) {
                    vm.isLoading = false;
                    if (response.data.status == 0) {
                        vm.CloseRequestForQuantity();
                        vm.ShowAlert(response.data.msg, "success", true, vm.$t('Alert'));

                    }
                    else {
                        vm.isLoading = false;
                        vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"), "failure", true, vm.$t('Alert'));
                        vm.onCancel();
                    };
                }
            });
        }
    },



}

</script>

<style scoped>
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

input[type="number"] {
    -moz-appearance: textfield;
}
</style>