<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">
                            {{ $t('AssetTransfer') }} : {{ ItemData.Name }}
                        </h5>
                        <button v-on:click="closeTransfer" href="javascript:;" class="close"><i class="fa fa-times"
                                aria-hidden="true"></i></button>
                    </div>
                    <div class="modal-body">
                        <div class="row">
                            <div class="col-sm-12 col-lg-12">
                                <div class="form-group">
                                    <label>Select Product</label>
                                    <div class="listing float-none">
                                        <div class="table-responsive">
                                            <table class="table table-bordered dt-responsive nowrap mt-0">
                                                <thead class="thead-bg">
                                                    <tr>

                                                        <!-- 
                                                        <th class="text-center">
                                                            <div class="pullleft"><input class="chkItems"
                                                                    type="checkbox" id="chkAll" name="checkbox" /></div>
                                                        </th> -->

                                                        <th class="text-center">
                                                            <div class="custom-control custom-checkbox"
                                                                @click="checkAllSearchRecord()">
                                                                <input class="chkItems custom-control-input"
                                                                    v-model="selectAll" type="checkbox" id="chkAll"
                                                                    name="checkbox"><label
                                                                    class="custom-control-label universal-custom-control-label pt-1"></label>
                                                            </div>

                                                        </th>

                                                        <th><span href="javascript:;" id="Item">{{ $t('Product')
                                                                }}</span>
                                                        </th>
                                                        <th><span href="javascript:;" id="UniqueName">{{
            $t('UniqueName') }}</span></th>
                                                        <th><span href="javascript:;" id="ItemCode">{{ $t('ItemCode')
                                                                }}</span></th>
                                                        <th class="text-center"><span href="javascript:;"
                                                                id="AvailableQuantity">{{
            $t('AVAILABLE_ITEMS') }}</span>
                                                        </th>
                                                        <th style="min-width:200px;"><span href="javascript:;"
                                                                id="Quantity">{{ $t('DivideLeaveQuantity') }}</span>
                                                        </th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    <template v-for="(item, index) in TransferData">
                                                        <tr class="insertedrow Records parentmain bg-gold">

                                                            <td class="text-center">
                                                                <div class="custom-control custom-checkbox"
                                                                    v-on:click="singleCheckRecord(item)">
                                                                    <input class="chkItems custom-control-input"
                                                                        type="checkbox" name="checkbox"
                                                                        style="display: none;"
                                                                        v-model="item.isSelected">
                                                                    <label
                                                                        class="custom-control-label universal-custom-control-label pt-1"></label>
                                                                </div>
                                                            </td>



                                                            <td>
                                                                <span :title="item.CatalogName"
                                                                    class="spnCatalogName">{{ item.CatalogName }}</span>
                                                            </td>
                                                            <td>
                                                                <span :title="item.UniqueName" class="spnUniqueName">{{
            item.UniqueName }}</span>
                                                            </td>
                                                            <td>
                                                                <span :title="item.ItemCode" class="spnItemCode">{{
            item.ItemCode }}</span>
                                                            </td>
                                                            <td class="text-center">
                                                                {{ item.AvailableItems }}
                                                            </td>
                                                            <td>
                                                                <Field v-if="item.ISUnique" name="Quantity"
                                                                    :rules="{ 'required': false }">
                                                                    <input type="text"
                                                                        class="txtAvailableItems form-control" disabled
                                                                        v-model="item.TransferQuantity"
                                                                        data-numberonly="yes">
                                                                </Field>

                                                                <Field v-else name="Quantity"
                                                                    :rules="{ 'required': true }" v-slot="{ errors }">

                                                                    <input type="number" min="1" maxlength="8"
                                                                        v-model="item.TransferQuantity"
                                                                        v-on:keyup="checkQuantity(item)"
                                                                        v-bind:class="{ 'txtAvailableItems form-control ': true, 'is-invalid': errors[0] || item.TransferQuantity == 0 ? true : false }" />
                                                                    <span v-if="errors[0]" name="Location"
                                                                        class="invalid-feedback d-block">{{ errors[0]
                                                                        }}</span>
                                                                </Field>

                                                            </td>
                                                        </tr>
                                                        <tr v-for="(child, childIndex) in index.AssetCatalogueGroup"
                                                            :key="childIndex">

                                                            <td class="text-center">
                                                                <div class="custom-control custom-checkbox">
                                                                    <input class="chkItems custom-control-input"
                                                                        type="checkbox" name="checkbox"
                                                                        style="display: none;">
                                                                    <label
                                                                        class="custom-control-label universal-custom-control-label pt-1"></label>
                                                                </div>
                                                            </td>

                                                            <td>
                                                                <span :title="child.CatalogName">{{ child.CatalogName
                                                                    }}</span>
                                                            </td>
                                                            <td>
                                                                <span :title="child.UniqueName">{{ child.UniqueName
                                                                    }}</span>
                                                            </td>
                                                            <td>
                                                                <span :title="child.ItemCode">{{ child.ItemCode
                                                                    }}</span>
                                                            </td>
                                                            <td>
                                                                <input v-if="item.ISUnique" type="text"
                                                                    class="txtAvailableItems form-control" disabled>
                                                                <input v-else type="text"
                                                                    class="txtAvailableItems form-control">
                                                            </td>
                                                        </tr>
                                                    </template>

                                                    <tr class="row">
                                                        <td v-if="NoRecordfound" colspan="7"
                                                            class="no_records no-record">{{ $t('No_Record_Found') }}
                                                        </td>
                                                    </tr>

                                                </tbody>
                                            </table>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <dynamic-form lang="en" ref="updateStatusField" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit">

                            <template v-slot:tgslot-Location="{ data }">

                                <Field name="Location" class="w-100" :rules="{ 'required': true }" v-slot="{ errors }">
                                    <div class="input-group">
                                        <tree-view v-model="data.fieldInfo.value" :options="options"
                                            v-on:input="GetLocationWiseUsers(data.fieldInfo.value)"
                                            placeholder="Select Location" :show-count="true"
                                            v-bind:class="{ '': true, 'is-invalid': errors[0] }">
                                        </tree-view>
                                    </div>
                                    <span v-if="errors[0]" name="Location" class="invalid-feedback d-block">{{
                                        errors[0] }}</span>
                                </Field>

                            </template>
                        </dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataService from '../../service/DataService';
import Treeselect from '@riophae/vue-treeselect';
import '@riophae/vue-treeselect/dist/vue-treeselect.css'

export default {

    components: {
        'tree-view': Treeselect
    },
    props: {
        ItemData: {
            type: Array,
            required: true
        }
    },
    data() {
        return {
            DataLoaded: 0,
            NoRecordfound: false,
            isLoading: false,
            TransferData: [],
            selectAll: false,
            selectedItems: [],
            options: [],
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "SlotField",
                            label: this.$t('Location'),
                            name: "Location",
                            value: null,
                            placeholder: "Select Location",

                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Users'),
                            name: "Users",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],

                            },
                            validationRules: {
                                "required": true
                            },
                        },
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('Remarks'),
                            name: "Remarks",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": false },
                            config: {
                                rows: 2
                            }
                        },

                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },

                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.closeTransfer
                }
            ],
            LocationData: [],
            UserData: [],
        }
    },
    async created() {

        await this.GetCompanyAssestCatalogsDetalis();
        await this.GetLocationByCompanyId();

    },
    methods: {
        GetLocationByCompanyId: async function () {
            var vm = this;
            var url = `reqForm=`;
            await DataService.GetLocationByCompanyId(url).then(response => {
                //debugger              
                vm.LocationData = JSON.parse(response.data).data;
                vm.FindalLocationArray = [];
                vm.LocationData.forEach((value) => {
                    var CheckIfExist = vm.FindalLocationArray.filter(function (el) {
                        return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                    });
                    if (CheckIfExist.length == 0) {
                        if (value.PARENT_LOCATION_ID == null) {
                            let locationId = value.COMPANY_LOCATION_ID
                            let rslt = vm.getChildLocation(locationId, value, vm.LocationData)
                            vm.FindalLocationArray.push(rslt)
                        }
                    }
                });

                vm.options = vm.FindalLocationArray;
            });

        },
        getChildLocation: function (locationId, currentItem, data) {
            let ths = this;
            let treeArray = {};
            treeArray = {
                id: currentItem.COMPANY_LOCATION_ID,
                label: currentItem.LOCATION_NAME
            }
            let retrunArray = treeArray;
            var childArray = data.filter(function (el) {
                return el.PARENT_LOCATION_ID == locationId;
            });
            let finalChildArray = [];
            childArray.forEach((value) => {
                let ChildfolderId = value.COMPANY_LOCATION_ID
                var CheckIfExist1 = finalChildArray.filter(function (el) {
                    return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                });
                if (CheckIfExist1.length == 0) {
                    var CheckIfExist = data.filter(function (el) {
                        return el.PARENT_LOCATION_ID == ChildfolderId;
                    });
                    if (CheckIfExist != null && CheckIfExist.length > 0) {
                        let rslt = ths.getChildLocation(ChildfolderId, value, data);
                        finalChildArray.push(rslt);
                    } else {
                        treeArray = {
                            id: `${value.COMPANY_LOCATION_ID}`,
                            label: `${value.LOCATION_NAME}`
                        }
                        finalChildArray.push(treeArray);
                    }
                }
            });
            retrunArray.children = finalChildArray;
            return retrunArray;
        },

        async GetLocationWiseUsers(location) {
            var vm = this;
            vm.isLoading = true;
            vm.UserData = [];
            vm.FormSchema[0].Data[1].config.options = [];

            var url = `locationId=${location}&moduleName=Asset`;
            await DataService.GetLocationWiseUsers(url).then(response => {

                if (response.data != null) {
                    vm.isLoading = false;
                    vm.UserData = response.data;
                    vm.UserData.forEach((item, index) => {
                        (vm.FormSchema)[0].Data[1].config.options.push({
                            name: item.Text,
                            value: `${item.Value}`
                        })
                    });
                    vm.$refs.updateStatusField.UpdateKeyValue();
                }

                else {
                    vm.isLoading = false;
                }

            })
        },
        async GetCompanyAssestCatalogsDetalis() {
            var url = `locationid=${this.ItemData.LocationId}&assetUniqueId=${this.ItemData.RelatedInfoId}&assetAssignmentId=${this.ItemData.AssetAssignmentId}`;
            await DataService.GetCompanyAssestCatalog(url).then(response => {
                this.TransferData.push(JSON.parse(response.data));
                this.TransferData.forEach(ele => {
                    ele.isSelected = false;
                    ele.TransferQuantity = ele.AvailableItems;
                })
            })
        },
        getData: function () {
            var Data = "";
            this.TransferData.forEach((value, index) => {
                var catalogId = this.ItemData.AssetCatalogueId;
                var relatedInfoId = value.RelatedInfoId;
                var quantity = value.AvailableItems;
                var assignmentId = value.AssetAssignmentId;
                if (Data == "") {
                    Data = catalogId + "," + relatedInfoId + "," + quantity + "," + assignmentId;
                }
                else {
                    Data = Data + "|" + catalogId + "," + relatedInfoId + "," + quantity + "," + assignmentId;
                }
            });
            return Data;
        },
        closeTransfer: function () {
            this.$emit('closeTransfer')
        },
        onCancel: function () {
            this.$emit('closeTransfer')
        },

        checkAllSearchRecord: function () {
            var vm = this;
            vm.selectAll = !vm.selectAll
            if (vm.selectAll) {
                vm.selectAll = true;
                vm.TransferData.forEach(element => {
                    element.isSelected = true;
                })
            } else {
                vm.TransferData.forEach(element => {
                    element.isSelected = false;
                })
            }
        },

        singleCheckRecord: function (item) {
            var vm = this;
            vm.selectAll = true;
            item.isSelected = !item.isSelected;
            if (item.isSelected) {
                vm.TransferData.forEach(element => {
                    if (!element.isSelected) {
                        vm.selectAll = false;
                    }
                })
            }
            else {
                vm.selectAll = false;
            }

        },
        checkQuantity: function (item) {
            var vm = this;
            let TotalQuantity = item.AvailableItems;
            let currentValue = item.TransferQuantity.replace(/[^0-9]/g, "");
            if (parseInt(currentValue) > TotalQuantity) {
                const getRecode = this.TransferData.find(x => x.AssetCatalogueId === item.AssetCatalogueId)
                getRecode.TransferQuantity = '';
                return vm.ShowAlert(vm.$t("Transfer quantity should not be greater than the available quantity."), "warning", vm.$t("Alert"));
            }
            else {
                const getRecode = this.TransferData.find(x => x.AssetCatalogueId === item.AssetCatalogueId)
                getRecode.TransferQuantity = currentValue.replace(/[^0-9]/g, "");
                return
            }
        },


        onSubmit: function (formfields, val) {
            var vm = this;
            vm.isLoading = true;
            let validationMessage = false;

            vm.TransferData.forEach(ele => {
                if (ele.isSelected == false) {
                    validationMessage = true;
                }

            })
            if (validationMessage == true) {
                vm.ShowAlert(vm.$t('PleaseSelectAtleastOneProduct'), "failure", true, vm.$t("Alert"));
                vm.isLoading = false;
                return;
            }

            var tranferData = vm.getData();

            if (tranferData == "") {
                vm.ShowAlert(vm.$t('PleaseSelectAtleastOneProduct'), "failure", true, vm.$t("Alert"));
                vm.isLoading = false;
            }
            else {
                let data = {
                    transferData: tranferData,
                    locationId: formfields.Location,
                    userId: formfields.Users,
                    comments: formfields.Remarks
                }
                var Jsondata = JSON.stringify(data);
                DataService.AssetTransferToEmployee(Jsondata).then(response => {
                    if (response.data.status == 0) {
                        vm.isLoading = false;

                        vm.closeTransfer();
                        vm.$parent.FetchData();
                        vm.ShowAlert('Product has been successfully Transferred', "success", true, vm.$t("Alert"));
                    }
                    else if (response.data.status == -1) {
                        vm.isLoading = false;
                        vm.ShowAlert(response.data.msg, "failure", true, vm.$t("Alert"));
                    } else {
                        vm.isLoading = false;
                        vm.ShowAlert(vm.$t('ErrorWhileProcessingRequest'), "failure", true, vm.$t("Alert"));
                    }
                });
            }
        }
    }
}
</script>

<style>
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

input[type="number"] {
    -moz-appearance: textfield;
}

#Location .vue-treeselect__input {
    border: none
}

#Location .vue-treeselect.is-invalid .vue-treeselect__control {
    border: 1px solid #dc3545 !important;
}
</style>