<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">{{ $t('IsSelfCheckOut') }} : {{ ItemData.Name }}
                        </h5>
                        <button v-on:click="CloseSelfCheckOut" type="button" class="close"
                            data-original-title="Close"><em class="fa fa-times"></em></button>


                    </div>
                    <div class="border p-3">

                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">

                            <template v-slot:tgslot-NumberofProducts="{ data }">

                             
                                    <Field name="Number of Products" :rules="{ 'required': true }" v-slot="{ errors }">
                                        <div class="input-group">
                                            <input type="number"
                                                v-on:keyup="checkQuantity($event, data.fieldInfo, data.fieldInfo.value)"
                                                id="" v-model="data.fieldInfo.value" min="1" maxlength="8"
                                                v-bind:class="{ 'txtitems form-control ': true, 'is-invalid': errors[0] || data.fieldInfo.NumberOfItems == 0 ? true : false }" />
                                            <span class="input-group-append">
                                                <span class="input-group-text" id="spnOutOF">/Out of {{
            ItemData.Available_Quantity }}</span>
                                            </span>
                                        </div>
                                        <span v-if="errors[0]" name="Number of Products"
                                            class="invalid-feedback d-block">{{ errors[0]
                                            }}</span>

                                    </Field>
                                

                            </template>

                        </dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import DataService from '../../service/DataService';
export default {
    props: {
        ItemData: {
            type: Array,
            required: true
        }
    },
    data() {
        return {
            isLoading: false,
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('Product'),
                            name: "Product",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],

                            },
                            disabled: true
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Unique Name'),
                            name: "10/12/2022 12:AM",
                            value: "",
                            placeholder: "",
                            disabled: true

                        },
                        {
                            astype: "TextField",
                            label: this.$t('AssignedFrom'),
                            name: "10/12/2022 12:AM",
                            value: '',
                            placeholder: "10/12/2022 12:AM",

                            config: { format: "MM/DD/YYYY" },
                            disabled: true
                        },

                        {
                            astype: "TextField",
                            label: this.$t('Assigned Till'),
                            name: "Assigned Till",
                            value: '',
                            placeholder: "M/D/YYYY",

                            config: { format: "MM/DD/YYYY" },
                            disabled: true
                        },

                        {
                            astype: "SlotField",
                            label: this.$t('Number of Products'),
                            name: "NumberofProducts",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 250 },

                        }
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('Remarks'),
                            name: "Remarks",
                            value: "",
                            placeholder: "",
                            config: {
                                rows: 5
                            }
                        },
                    ]
                }

            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.CloseSelfCheckOut
                }
            ],
        }
    },
    created: function () {
        this.FetchData();
    },
    methods: {
        onSubmit: function (formfields) {
            debugger;
            var vm = this;
            vm.isLoading = true;
            let Objdata = {
                Name: vm.ItemData.Name,
                UniqueName: vm.ItemData.UniqueName,
                AssignedFrom: vm.ItemData.AssignedFrom,
                AssignedTill: vm.ItemData.AssignedTill,
                NumberOfItems: formfields.NumberofProducts,
                Remarks: formfields.Remarks,
                RelatedInfoId: vm.ItemData.RelatedInfoId,
                AssetAssignmentId: vm.ItemData.AssetAssignmentId,
                UserId: vm.ItemData.UserId,
                AssetCatalogueId: vm.ItemData.AssetCatalogueId,
                RequestType: 'SELFCHECKOUT'
            }
            var JsonObj = JSON.stringify(Objdata);
            DataService.AssetSelfCheckOut(JsonObj).then(response => {

                if (response.data != null) {
                    if (response.data.status == 0) {

                        vm.isLoading = false;
                        vm.ShowAlert(response.data.msg, "success", true, vm.$t('Alert'));
                        vm.CloseSelfCheckOut();

                    }
                    else if (response.data.status == -1) {
                        vm.isLoading = false;
                        vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"), "failure", true, vm.$t('Alert'));
                        return;

                    }

                } else {
                    vm.isLoading = false;
                    vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"), "failure", true, vm.$t('Alert'));
                    vm.CloseSelfCheckOut();
                }
            });
        },
        CloseSelfCheckOut: function () {
            this.$emit('CloseSelfCheckOut');
        },
        FetchData: function () {
            var vm = this;
            (vm.FormSchema)[0].Data[0].value = vm.ItemData.Name;
            (vm.FormSchema)[0].Data[1].value = vm.ItemData.UniqueName;
            (vm.FormSchema)[0].Data[2].value = vm.ItemData.AssignedFrom;
            (vm.FormSchema)[0].Data[3].value = vm.ItemData.AssignedTill;
            // (vm.FormSchema)[0].Data[4].value = vm.ItemData.NumberOfItems;
        },

        checkQuantity(event, data, value) {
            debugger;

            var vm = this;
            let currentQuantity = value;

            if (parseInt(currentQuantity) > vm.ItemData.Available_Quantity) {
                ////
                vm.FormSchema[0].Data[4].value = null;
                vm.ShowAlert(vm.$t('Please enter a value less than or equal to Available quantity'), "failure", true, vm.$t('Alert'));

            }
            else {
                //
                vm.FormSchema[0].Data[4].value = currentQuantity;
                return
            }
        }
    }
}
</script>

<style scoped>
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

input[type="number"] {
    -moz-appearance: textfield;
}
</style>